.div_home {
    height: 100%;
}

.div_my_inform{
    height: 96%;
    overflow: auto;
    padding: 1%;
    scroll-behavior: smooth;
}

.div_my_inform::-webkit-scrollbar {
    width: 7px;
}

.div_my_inform::-webkit-scrollbar-track {
    background: lightgray;
}

.div_my_inform::-webkit-scrollbar-thumb {
    background-color: black;
    border-radius: 50px;
    border: 1px solid rgb(255, 249, 249);
}

.my_photo {
    height: 150px;
    width: 150px;
    margin: 1%;
    border-radius: 64%;
}

.my_name {
    display: flex;
    flex-direction: column;
    align-items: center;
}